/*
Movie.js에서
<h3 class="movie_title" style={{backgroundColor:Red}}>{title}</h3>
이렇게 스타일을 넣어줘도 되지만 css파일을 따로 생성하는것을 추천

이후 Movie.js 파일에서
import "./Movie.css"; 해줘야함
*/

.movie img {  
    position: relative;
    top:-50px;
    max-width: 170px;
    width: 100%;
    margin-right: 30px;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25),
    0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);

}

.movie .movie_title, 
.movie .movie_year {
    font-weight: 300;
    margin: 0; 
}

.movie .movie_title{
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .movie_genres {
   
   list-style: none;
   padding: 0;
   margin: 0;
   display: flex;
   margin: 5px 0px;
}

.movie_genres li,
.movie .movie_year{
    margin-right: 10px;
    font-size: 14px;

} 